import Vue from "vue";
import Vuex from "vuex";
import createVuexAlong from "vuex-along";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    loading: false,
    user: undefined,
  },
  mutations: {
    setLoading(state, loading) {
      state.loading = loading;
    },
    setUser(state, user) {
      state.user = user;
    },
  },
  actions: {},
  modules: {},
  plugins: [
    createVuexAlong({
      name: "yunchengly",
      local: {
        list: ["user"],
      },
    }),
  ],
});
